import { Block, Controller, Inline } from '@ali/4ever-cangjie';
import { Heading, Paragraph } from '@ali/4ever-mo';
import { getBlocksFromEditor }  from '@ali/4ever-utils';

const DEFAULT_ALIGNMENT = 'left';

function getAlignmentOfBlock(block: Block) {
  const alignment = block
    ? block.data.jc || DEFAULT_ALIGNMENT
    : DEFAULT_ALIGNMENT;
  return alignment === 'distribute'
    ? 'justify'
    : alignment;
}

export default function getAlignment(controller: Controller, node?: Inline | Block) {
  let paragraphs;
  if (node) {
    const curBlock = controller.value.document.getClosestBlock(node.key);
    paragraphs = curBlock ? [curBlock] : [];
  } else {
    const blocks = getBlocksFromEditor(controller);
    paragraphs = blocks.filter(
      block => Paragraph.isParagraph(block) || Heading.isHeading(block),
    );
  }
  if (paragraphs.length === 1) {
    return getAlignmentOfBlock(paragraphs[0]);
  }

  let commonAlignment;
  paragraphs.some(paragraph => {
    const currentAlignment = getAlignmentOfBlock(paragraph);
    if (commonAlignment && currentAlignment !== commonAlignment) {
      commonAlignment = undefined;
      return true;
    }
    commonAlignment = currentAlignment;
    return false;
  });

  return commonAlignment;
}
